import React, { ReactElement } from "react";
import {
	ClearIndicatorProps,
	components as selectComponents,
	ControlProps,
	GroupBase,
	GroupHeadingProps,
	MultiValueProps,
	OptionProps,
} from "react-select";

import { Icon } from "../Icon/Icon";
import { colors } from "../styleguide";
import Tag from "../Tag/Tag";
import { Typography } from "../Typography";

import { OptionContentWrapper, OptionIcon } from "./Combobox.styles";
import { OptionType } from "./Combobox.types";

export const Control = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(props: ControlProps<Option, IsMulti, Group>): ReactElement => {
	const getComboboxLeftIcon = () => {
		if (props.selectProps.comboboxIcon) {
			return <Icon name={props.selectProps.comboboxIcon} size="20" />;
		} else if (props.selectProps.comboboxLeftIcon) {
			return <Icon name={props.selectProps.comboboxLeftIcon} size="20" />;
		}
		return;
	};

	return (
		<selectComponents.Control {...props}>
			{getComboboxLeftIcon()}
			{props.children}
			{props.selectProps.validation === "valid" && <Icon name="Check" size="20" color={colors.green} />}
			{props.selectProps.comboboxRightIcon && <Icon name={props.selectProps.comboboxRightIcon} size="20" />}
		</selectComponents.Control>
	);
};

export const ClearIndicator = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(props: ClearIndicatorProps<Option, IsMulti, Group>): ReactElement => (
		<selectComponents.ClearIndicator {...props}>
			<Icon name="X" size="20" />
		</selectComponents.ClearIndicator>
	);

export const Option = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(props: OptionProps<Option, IsMulti, Group>): ReactElement => {
	const { data, children } = props;
	const optionData = data;

	return (
		<selectComponents.Option {...props}>
			<OptionContentWrapper>
				{optionData.leftIcon && (
					<OptionIcon
						name={optionData.leftIcon}
						size="20"
					/>
				)}
				{children}
			</OptionContentWrapper>
			{props.isSelected && props.isMulti && <Icon name="Check" size="20" />}
		</selectComponents.Option>
	);
};

export const MultiValue = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(props: MultiValueProps<Option, IsMulti, Group>): ReactElement => {
	const { removeProps, data } = props;
	const optionData = data;
	const { onClick } = removeProps;
	return (
		<selectComponents.MultiValueContainer {...props}>
			{(optionData.label && optionData.value) && (
				<Tag
					icon={optionData.leftIcon}
					dataQA={`${optionData.value}-combobox-tag`}
					text={optionData.label}
					onDelete={(e: React.MouseEvent<HTMLInputElement>) => onClick && onClick(e)}
				/>
			)}
		</selectComponents.MultiValueContainer>
	);
};

export const GroupHeading = <
	Option extends OptionType,
	IsMulti extends boolean,
	Group extends GroupBase<Option> = GroupBase<Option>,
>(props: GroupHeadingProps<Option, IsMulti, Group>): ReactElement => (
		<selectComponents.GroupHeading {...props}>
			<Typography
				dataQA="group-heading"
				component="span"
				variant="caption"
				color={`${colors.black}A8`}
			>
				{props.children}
			</Typography>
		</selectComponents.GroupHeading>
	);
